<template>
  <!-- 唤友榜单 -->
  <div class="page">
    <div class="bg">
      <img src="~@/assets/invite/bg@2x.png" alt="" />
    </div>
    <div class="main">
      <div class="name justify-center">
        <img src="~@/assets/invite/name@2x.png" alt="" />
      </div>
      <div class="tabs justify-center">
        <div class="tabi taba curm">本月榜单</div>
        <div class="tabi lastm">上月榜单</div>
      </div>
      <div class="content">
        <div class="bgbottom justify-center">
          <div class="bgtop">
            <div class="sort-wrap">
              <div class="sort-title">
                <div class="sort-num justify-center">
                  <div class="">排名</div>
                </div>
                <div class="sort-name sort-text align-center">用户</div>
                <div class="invite-count sort-text align-center">唤友数</div>
              </div>
              <div class="my-sort">
                <div class="sort-num justify-center">
                  <div v-if="mySort.sort > 9999" class="my-sort-digital">
                    9999+
                  </div>
                  <div v-else class="sort-digital">
                    {{ mySort.sort }}
                  </div>
                </div>
                <div class="sort-name sort-text align-center">
                  {{ $store.getters.userInfo.nickname }}
                </div>
                <div
                  class="invite-count sort-text align-center"
                  @click="goList"
                >
                  {{ mySort.inviteNum }}
                  <img class="more" src="~@/assets/invite/more@2x.png" alt="" />
                </div>
              </div>
              <div class="sort-box">
                <div
                  class="sorti"
                  v-for="(sort, index) in sortList"
                  :key="index"
                >
                  <div class="sort-num justify-center">
                    <div v-if="index === 0" class="sort-top">
                      <img
                        class="sortimg"
                        src="~@/assets/invite/sort1@2x.png"
                        alt=""
                      />
                    </div>
                    <div v-else-if="index === 1" class="sort-top">
                      <img
                        class="sortimg"
                        src="~@/assets/invite/sort2@2x.png"
                        alt=""
                      />
                    </div>
                    <div v-else-if="index === 2" class="sort-top">
                      <img
                        class="sortimg"
                        src="~@/assets/invite/sort3@2x.png"
                        alt=""
                      />
                    </div>
                    <div v-else class="sort-digital">{{ sort.sort }}</div>
                  </div>
                  <div class="sort-name sort-text align-center">
                    {{ sort.name }}
                  </div>
                  <div class="invite-count sort-text align-center">
                    {{ sort.inviteNum }}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="invite justify-center">
        <img src="~@/assets/invite/invite@2x.png" alt="" />
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      sortList: [
        {
          sort: 1,
          name: '张三',
          inviteNum: 2222304
        },
        {
          sort: 2,
          name: '中文八个字符测试',
          inviteNum: 230333
        },
        {
          sort: 3,
          name: '12345678912345678',
          inviteNum: 23304
        },
        {
          sort: 4,
          name: '张三',
          inviteNum: 2304
        },
        {
          sort: 5,
          name: '张三',
          inviteNum: 2304
        },
        {
          sort: 6,
          name: '张三',
          inviteNum: 23
        },
        {
          sort: 7,
          name: '张三',
          inviteNum: 23
        },
        {
          sort: 8,
          name: '张三',
          inviteNum: 23
        },
        {
          sort: 9,
          name: '张三',
          inviteNum: 23
        },
        {
          sort: 10,
          name: '张三',
          inviteNum: 21
        }
      ],
      mySort: {
        sort: 12000,
        inviteNum: 2
      }

    }
  },

  methods: {

    goList () {
      this.$router.push('invitationList')
    }

  }
}
</script>

<style lang="less" scoped>
@import url("../assets/css/mixin.less");
.page {
  min-height: 100%;
  width: 100%;
  background-color: #e77f6b;
  overflow-y: auto;
  position: relative;
  .bg {
    img {
      width: 100%;
    }
  }
  .invite {
    margin-top: 30px;
    font-size: 30px;
    color: #f2cf8d;
    text-align: center;
  }
  .main {
    z-index: 2;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    .name {
      width: 100%;
      height: 250px;
      padding-top: 60px;
      > img {
        width: 586px;
        height: 190px;
      }
    }
    .tabs {
      width: 100%;
      height: 80px;
      margin-top: 26px;
      .tabi {
        width: 280px;
        height: 80px;
        line-height: 80px;
        text-align: center;
        .bg-image("~@/assets/invite/btn1","png");
        font-size: 32px;
        font-weight: 550;
        font-family: PingFangSC-Regular, PingFang SC;
        color: #e44122;
      }
      .taba {
        .bg-image("~@/assets/invite/btn","png");
        color: #fff;
      }
      .lastm {
        margin-left: 16px;
      }
    }
    .content {
      width: 100%;
      padding: 0 32px 8px;
      margin-top: 32px;
      .flex_func(column, center,center,nowrap);
      .bgbottom {
        width: 686px;
        min-height: 200px;
        border-radius: 20px;
        background: url(~@/assets/home/sort_cont_bg2@2x.png) no-repeat;
        background-size: 100% 100%;
        background-position: center;
        padding: 0 0 13px;
        // border: 2px solid #feeebc;
        .bgtop {
          width: 654px;
          height: 100%;
          min-height: 240px;

          z-index: 2;
          border-radius: 20px;
        }
        .sort-wrap {
          width: 100%;
          min-height: 200px;
          border-radius: 16px;
          margin-top: 20px;
          .sort-title {
            width: 100%;
            height: 88px;
            line-height: 88px;
            padding: 0 56px;
            font-size: 28px;
            font-family: PingFangSC-Medium, PingFang SC;
            font-weight: 500;
            color: #fff;
            .flex_func(row,space-between,center,nowrap);
            .sort-text {
              color: #fff;
            }
          }
          .my-sort {
            height: 96px;
            width: 100%;
            padding: 0 56px;
            background: #fff2dc;
            border-top-left-radius: 16px;
            border-top-right-radius: 16px;
            .flex_func(row,space-between,center,nowrap);
            .my-sort-digital {
              border-radius: 20px;
              border: 1px solid #ead5d5;
              font-size: 24px;
              font-family: PingFangSC-Medium, PingFang SC;
              font-weight: 500;
              color: #cc6d5b;
              text-align: center;
              line-height: 40px;
            }
            .sort-text {
              color: #ff482a;
            }
            .more {
              width: 24px;
              height: 24px;
              margin-left: 10px;
            }
          }
          .sort-box {
            padding: 0 56px;
            background: #fef0f2;
            border-bottom-left-radius: 16px;
            border-bottom-right-radius: 16px;
            .sorti {
              width: 100%;
              height: 96px;
              border-top: 2px solid #efefef;
              .flex_func(row,space-between,center,nowrap);
            }
          }
          .sort-num {
            width: 100px;
            .sort-top {
              .sortimg {
                width: 60px;
                height: 76px;
              }
            }
            .sort-digital {
              width: 40px;
              height: 40px;
              border-radius: 20px;
              border: 1px solid #ead5d5;
              font-size: 24px;
              font-family: PingFangSC-Medium, PingFang SC;
              font-weight: 500;
              color: #cc6d5b;
              text-align: center;
              line-height: 40px;
            }
          }
          .sort-name {
            width: calc(100% - 190px);
            height: 100%;
            padding-left: 15px;
          }
          .invite-count {
            width: 90px;
            height: 100%;
          }
          .sort-text {
            font-size: 28px;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: #333333;
            text-align: left;
          }
        }
      }
      .box {
        width: 686px;
        height: 100%;
        padding: 32px;
        z-index: 2;
        background: #fef0f2;
        border-radius: 16px;
        font-size: 24px;
        font-family: PingFangSC-Regular, PingFang SC;
        color: #333333;
        line-height: 34px;
        p {
          line-height: 34px;
        }
        .pcenter {
          margin: 30px 0;
        }
      }
    }

    .invite {
      width: 100%;
      height: 128px;
      margin-bottom: 8px;
      > img {
        width: 596px;
        height: 128px;
      }
    }
  }
}
</style>



